.signup {
  background: $grey-200;
  margin-top: 3rem;
  color: $grey-700;
  @include mq(sm) {
    margin-top: 0;
  }

  .content {
    padding: 2rem 1.8rem;


    h2 {
      font-weight: 400;
      font-size: 2rem;
      line-height: 1.1;
      margin-bottom: 1rem;
    }
  }

  .input-group {
    display: flex;
    flex-direction: column;
    @include mq(md) {
      display: inline-flex;
      width: 48%;
      margin-right: 1.75rem;
    }
  }

  label.required {
    font-weight: 600;
    line-height: 1.9;

    &::after {
      content: '*';
      color: #d24d57;
    }
  }

  input[type="text"] {
    padding: 0.3125rem 0.9375rem;
    height: 3rem;
    border: 1px solid $grey-300;
    margin-bottom: 1rem;
    &:focus {
      border-color: #5bc0de;
      outline: 0;
      box-shadow: inset 0 1px 1px rgba(0, 0, 0,  0.8), 0 0 8px rgba(91, 192, 222, 0.60);
    }
  }

}

.subscribe {
  margin-top: 3rem;
  margin-bottom: 2rem;

  @include mq(md) {
    margin-top: 2rem;
  }
}

.label > label {
  cursor: pointer;
  font-weight: 600;
  line-height: 2;
}

.check-holder {
  display: flex;

  .check {
    flex-grow: 0;
    flex-shrink: 0;
    height: 2em;
    width: 2em;
    border: 3px solid $grey-700;
    display: block;
    border-radius: 3px;
    margin-right: 1.25em;
    position: relative;

  }
  input {
    display: none;
    &:checked + .check {
      background: $grey-700;
      &::before {
        font-family: "Font Awesome 5 Free";
        content: '\f00c';
        color: $white;
        position: absolute;
        left: 5px;
        top: -2px;
      }
    }
  }

  .text a {
    text-decoration: underline;
  }
}
.input-group + .input-group {
  margin-right: 0;
}
